@charset "UTF-8";
*, *::after, *::before {
  box-sizing: inherit; }

.ui-label {
  font-family: Roboto, "-apple-system", "system-ui", BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  color: #333333;
  font-size: 11px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.6);
  font-weight: 500;
  line-height: 12px;
  margin-bottom: 4px; }

.ui-ellipsis {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.ui-paper {
  font-family: Roboto, "-apple-system", "system-ui", BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  color: #333333;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2); }

.ui-toaster-container {
  font-family: Roboto, "-apple-system", "system-ui", BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  color: #333333;
  line-height: 1.4;
  box-sizing: border-box;
  position: fixed;
  display: flex;
  flex-direction: column;
  right: 16px;
  top: 64px;
  z-index: 999; }
  .ui-toaster-container .ui-toastr-item {
    position: relative;
    width: 320px;
    background-color: #ffffff;
    padding: 16px;
    border-radius: 4px;
    font-size: 14px;
    margin-top: 8px;
    min-height: 48px;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
    animation: toastrAnimation 0.25s;
    transition: margin-top 0.25s, opacity 0.25s, height 0.25s; }
    .ui-toaster-container .ui-toastr-item .ui-toastr-message {
      font-weight: bold;
      margin-right: 24px; }
      .ui-toaster-container .ui-toastr-item .ui-toastr-message::before {
        display: inline-block;
        margin-right: 4px; }
    .ui-toaster-container .ui-toastr-item .ui-toastr-description {
      margin-top: 8px;
      font-size: 13px;
      line-height: 1.2; }
    .ui-toaster-container .ui-toastr-item.ui-toastr-item-hide-animation {
      height: 48px;
      margin-top: -48px;
      opacity: 0; }
    .ui-toaster-container .ui-toastr-item .ui-button {
      margin-top: 16px; }
  .ui-toaster-container .ui-toastr-item-type-info .ui-toastr-message::before {
    content: 'ℹ️'; }
  .ui-toaster-container .ui-toastr-item-type-success .ui-toastr-message {
    color: #83C833; }
    .ui-toaster-container .ui-toastr-item-type-success .ui-toastr-message::before {
      content: '✅'; }
  .ui-toaster-container .ui-toastr-item-type-warning .ui-toastr-message {
    color: #F6A622; }
    .ui-toaster-container .ui-toastr-item-type-warning .ui-toastr-message::before {
      content: '⚠️️'; }
  .ui-toaster-container .ui-toastr-item-type-danger .ui-toastr-message {
    color: #F53C3C; }
    .ui-toaster-container .ui-toastr-item-type-danger .ui-toastr-message::before {
      content: '🔴'; }
  .ui-toaster-container .ui-toastr-close {
    position: absolute;
    top: 11px;
    right: 11px;
    cursor: pointer;
    transition: background-color 0.25s;
    border-radius: 50%; }
    .ui-toaster-container .ui-toastr-close:hover {
      background-color: #D6D6D6; }
    .ui-toaster-container .ui-toastr-close svg {
      width: 26px;
      height: 26px;
      display: block; }

@keyframes toastrAnimation {
  from {
    transform: translate(100%, 0); } }
